/** @format */

import React, { Fragment, useEffect, useState, useRef } from 'react';
import {generate, mothSilky} from '../assets/random';
import baseSrc from '../images/butterflysilky-base.png';
import baseShadeSrc from '../images/butterflysilky-base-shade.png';
// import underbellySrc from '../images/butterfly-silky-underbelly.png';
// import underbellyShadeSrc from '../images/butterfly-silky-underbelly-shade.png';
import lineSrc from '../images/butterflysilky-line.png';
// import tailSrc from '../images/butterflysilky-tailfade.png';

const RandomCanvas = (props) => {
	const { setClick, click } = props;
	const [dragon, setDragon] = useState(null);

	const c1 = useRef(null);
	const c2 = useRef(null);
	//
	const cm0 = useRef(null);
	const cm00 = useRef(null);
	const cm1 = useRef(null);
	const cm11 = useRef(null);
	const cm2 = useRef(null);
	const cm22 = useRef(null);
	const cm3 = useRef(null);
	const cm33 = useRef(null);
	//
	const cx = useRef(null);

	useEffect(() => {
		if (click) {
						setClick(false)
						setDragon(null)
					}
		if (!dragon) {
			let newDragon = null
			console.log('no dragon');
			setDragon(generate(mothSilky))
		}
		if (dragon) {
			draw();
		}
	}, [dragon,click]);

	const draw = () => {
		console.log(dragon);
		let canvas = c1.current;
		let canvas2 = c2.current;
		//
		let canvas3 = cm0.current
		let canvas4 = cm00.current
		let canvas5 = cm1.current
		let canvas6 = cm11.current
		let canvas7 = cm2.current
		let canvas8 = cm22.current
		let canvas9 = cm3.current
		let canvas10 = cm33.current
		//
		let canvasX = cx.current;

		let ctx = canvas.getContext('2d');
		let ctx2 = canvas2.getContext('2d');
		let ctx3 = canvas3.getContext('2d');
		let ctx4 = canvas4.getContext('2d');
		let ctx5 = canvas5.getContext('2d');
		let ctx6 = canvas6.getContext('2d');
		let ctx7 = canvas7.getContext('2d');
		let ctx8 = canvas8.getContext('2d');
		let ctx9 = canvas9.getContext('2d');
		let ctx10 = canvas10.getContext('2d');
		let ctxX = canvasX.getContext('2d');

		loadImage(
			baseSrc,
			ctx,
			`rgb(${dragon.base.base[0]},${dragon.base.base[1]},${dragon.base.base[2]})`
		);
		loadImage(
			baseShadeSrc,
			ctx2,
			`rgb(${dragon.base.line[0]},${dragon.base.line[1]},${dragon.base.line[2]})`
		);
		loadImage(lineSrc, ctxX, `#0A072E`);
		if (dragon.markings.length > 0) {
			console.log(dragon.markings[0].type.baseSrc)
			loadImage(
				dragon.markings[0].type.baseSrc,
				ctx3,
				`rgb(${dragon.markings[0].color.base[0]},${dragon.markings[0].color.base[1]},${dragon.markings[0].color.base[2]})`
			);
			loadImage(
				dragon.markings[0].type.shadeSrc,
				ctx4,
				`rgb(${dragon.markings[0].color.line[0]},${dragon.markings[0].color.line[1]},${dragon.markings[0].color.line[2]})`
			);
		}
		if( dragon.markings.length > 1) {
			loadImage(
				dragon.markings[1].type.baseSrc,
				ctx5,
				`rgb(${dragon.markings[1].color.base[0]},${dragon.markings[1].color.base[1]},${dragon.markings[1].color.base[2]})`
			);
			loadImage(
				dragon.markings[1].type.shadeSrc,
				ctx6,
				`rgb(${dragon.markings[1].color.line[0]},${dragon.markings[1].color.line[1]},${dragon.markings[1].color.line[2]})`
			);
		}
		if( dragon.markings.length > 2) {
			loadImage(
				dragon.markings[2].type.baseSrc,
				ctx7,
				`rgb(${dragon.markings[2].color.base[0]},${dragon.markings[2].color.base[1]},${dragon.markings[2].color.base[2]})`
			);
			loadImage(
				dragon.markings[2].type.shadeSrc,
				ctx8,
				`rgb(${dragon.markings[2].color.line[0]},${dragon.markings[2].color.line[1]},${dragon.markings[2].color.line[2]})`
			);
		}
		if( dragon.markings.length > 3) {
			loadImage(
				dragon.markings[3].type.baseSrc,
				ctx9,
				`rgb(${dragon.markings[3].color.base[0]},${dragon.markings[3].color.base[1]},${dragon.markings[3].color.base[2]})`
			);
			loadImage(
				dragon.markings[3].type.shadeSrc,
				ctx10,
				`rgb(${dragon.markings[3].color.line[0]},${dragon.markings[3].color.line[1]},${dragon.markings[3].color.line[2]})`
			);
		}
	};

	const loadImage = (src, ctx, color) => {
		let image = new Image();
		image.src = src;
		image.onload = () => {
			ctx.clearRect(0, 0, image.width, image.height);
			ctx.globalCompositeOperation = 'source-over';
			ctx.drawImage(image, 0, 0, ctx.width, ctx.height);

			ctx.fillStyle = color;
			ctx.fillRect(0, 0, image.width, image.height);

			// clip
			ctx.globalCompositeOperation = 'destination-in';
			ctx.drawImage(image, 0, 0, image.width, image.height);

			// reset comp. mode to default
			ctx.globalCompositeOperation = 'source-over';
		};
	};

	return (
		<Fragment>
			{/* {mainColor && (
				<h1 className="u-margin-top-3">base: {mainColor.name}</h1>
			)}
			{markingColor && (
				<h1>marking: {markingColor.name}</h1>
			)}
			{tailColor && (
				<h1>marking: {tailColor.name}</h1>
			)} */}
			<div className="main__canvas">
				<canvas
					className="main__canvas--c1"
					ref={c1}
					width={200}
					height={200}
				></canvas>
				<canvas
					className="main__canvas--c2"
					ref={c2}
					width={200}
					height={200}
				></canvas>
				<canvas
					className="main__canvas--c3"
					ref={cm0}
					width={200}
					height={200}
				></canvas>
				<canvas
					className="main__canvas--c4"
					ref={cm00}
					width={200}
					height={200}
				></canvas>
				<canvas
					className="main__canvas--c5"
					ref={cm1}
					width={200}
					height={200}
				></canvas>
				<canvas
					className="main__canvas--c6"
					ref={cm11}
					width={200}
					height={200}
				></canvas>
				<canvas
					className="main__canvas--c7"
					ref={cm2}
					width={200}
					height={200}
				></canvas>
				<canvas
					className="main__canvas--c8"
					ref={cm22}
					width={200}
					height={200}
				></canvas>
				<canvas
					className="main__canvas--c9"
					ref={cm3}
					width={200}
					height={200}
				></canvas>
				<canvas
					className="main__canvas--c10"
					ref={cm33}
					width={200}
					height={200}
				></canvas>
				<canvas
					className="main__canvas--cx"
					ref={cx}
					width={200}
					height={200}
				></canvas>
			</div>
		</Fragment>
	);
};

export default RandomCanvas;
