import React, { Fragment, useState } from 'react'

// import RandomizeCanvas from './RandomizeCanvas';
import RandomCanvas from './RandomCanvas'
import Button from './Button'

const MonsterBox = () => {
    const [ click, setClick ] = useState(false)

    return (
        <Fragment>
            <div className="main">
                <RandomCanvas setClick={setClick} click={click}/>
            {/* <RandomizeCanvas setClick={setClick} click={click} /> */}
            </div>
            <Button click={setClick} />
        </Fragment>
    )
}

export default MonsterBox
